import { Component, OnInit, ViewChild, ViewEncapsulation } from "@angular/core";
import { FormGroup, FormControl } from "@angular/forms";
import { DatePickerValidator } from "@app/shared/utils/datePickerValidator";
import { MatAutocompleteSelectedEvent } from "@angular/material/autocomplete";
import { MatDialog } from "@angular/material/dialog";
import { MatTable } from "@angular/material/table";
import { GoogleLocation } from "@app/shared/model/google-location";
import { LocationService } from "@app/shared/service/location.service";
import { Observable, of, Subscription, merge } from "rxjs";
import { switchMap, map, catchError, tap, startWith } from "rxjs/operators";
import { GeoLocationService } from "@app/shared/service/geo-location.service";
import { MapsAPILoader, AgmMap } from "@agm/core";
import * as moment from "moment";
import {
  SearchDto,
  SearchParams,
  MatchWithLocationDto
} from "@app/shared/model/playing-field";
import { MatchService } from "@app/shared/service/match.service";
import { DataSharingService } from "@app/shared/service/data-sharing.service";
import { PlayingFieldService } from "@app/shared/service/playing-field.service";
import { UserService } from "@app/shared/service/user.service";
import { AuthService } from "@app/core/service";
import { TeamsDialogComponent } from "@app/functionalities/playing-field/components/teams-dialog/teams-dialog.component";
import { TeamService } from "@app/shared/service/team.service";
import { ReservationDialogComponent } from "@app/functionalities/playing-field/components/reservation-dialog/reservation-dialog.component";
import { BanService } from "@app/shared/service/ban.service";
import { MatPaginator, MatSort } from "@angular/material";
import { ToastrService } from "ngx-toastr";
import { Router } from "@angular/router";

@Component({
  selector: "app-search-match",
  templateUrl: "./search-match.component.html",
  styleUrls: ["./search-match.component.scss"]
})
export class SearchMatchComponent implements OnInit {
  minFromDate = new Date();
  maxToDate = new Date().setDate(2);
  searchForm: FormGroup;
  searchedLocations: any[];
  locations: Observable<any[]>;
  geoCoder: any;
  subscription: Subscription;
  selectedLocation: GoogleLocation;
  selectedLocation2: GoogleLocation;
  matchesWithLocationDto: MatchWithLocationDto[];
  loading = false;
  loggedUser;
  @ViewChild(MatTable, { static: false }) matchWithLocationDtoTable: MatTable<
    MatchWithLocationDto[]
  >;
  @ViewChild(MatPaginator, { static: true }) matchPaginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  matchLength: number;
  userLocation: GoogleLocation = {
    lat: 0,
    lng: 0,
    zoom: 13
  };
  displayedColumns: string[] = [
    "name",
    "address",
    "matchFrom",
    "matchTo",
    "private",
    "reserved",
    "size",
    "rate",
    "options"
  ];

  @ViewChild(AgmMap, { static: true }) map: AgmMap;

  constructor(
    private locationService: LocationService,
    private geoLocationService: GeoLocationService,
    private mapsApiLoader: MapsAPILoader,
    private matchService: MatchService,
    private userService: UserService,
    public dialog: MatDialog,
    private authService: AuthService,
    private teamService: TeamService,
    private dataSharingService: DataSharingService,
    private toastrService: ToastrService,
    private router: Router
  ) {
    this.mapsApiLoader.load().then(() => {
      this.geoCoder = new google.maps.Geocoder();
    });
  }

  ngOnInit() {
    this.loading = true;
    this.initForm();
    this.getLoggedUser();
    this.subscription = this.subscribeUserSelectedLocation();
    this.locations = this.handleSelectedLocationChange();
    this.getUserLocation();
  }

  initForm() {
    const searchParams = JSON.parse(localStorage.getItem("searchParams"));

    if (searchParams) {
      if (
        searchParams.searchDto.fromDate <
        moment(new Date()).format("YYYY-MM-DD")
      ) {
        searchParams.searchDto.fromDate = moment(new Date()).format(
          "YYYY-MM-DD"
        );
      }
      this.searchForm = new FormGroup({
        fromDate: new FormControl(
          searchParams.searchDto.fromDate,
          DatePickerValidator.fromDateValidator
        ),
        toDate: new FormControl(
          searchParams.searchDto.toDate,
          DatePickerValidator.ToDateValidator
        ),
        fromTime: new FormControl(searchParams.searchDto.fromTime),
        toTime: new FormControl(searchParams.searchDto.toTime),
        showPrivate: new FormControl(searchParams.searchDto.showPrivate),
        showFull: new FormControl(searchParams.searchDto.showFull),
        reserved: new FormControl(searchParams.searchDto.reserved),
        myLocalization: new FormControl(true),
        city: new FormControl({ value: "", disabled: "true" }),
        rangeInKm: new FormControl(searchParams.rangeInKm)
      });
    } else {
      const defaultToDate = moment(new Date(), "YYYY-MM-DD").add(5, "days");
      this.searchForm = new FormGroup({
        fromDate: new FormControl(
          new Date(),
          DatePickerValidator.fromDateValidator
        ),
        toDate: new FormControl(
          defaultToDate,
          DatePickerValidator.ToDateValidator
        ),
        fromTime: new FormControl("14:00"),
        toTime: new FormControl("18:00"),
        showPrivate: new FormControl(false),
        showFull: new FormControl(false),
        reserved: new FormControl(1),
        myLocalization: new FormControl(true),
        city: new FormControl({ value: "", disabled: "true" }),
        rangeInKm: new FormControl(1)
      });
    }
  }

  setLocation($event: MatAutocompleteSelectedEvent) {
    const location: string = $event.option.value;
    const index = this.searchedLocations.findIndex(
      elem => elem.formatted_address === location
    );

    if (index !== -1) {
      const loc: google.maps.GeocoderResult = this.searchedLocations[index];
      const selectedLocation: GoogleLocation = {
        lat: loc.geometry.location.lat(),
        lng: loc.geometry.location.lng(),
        zoom: 13
      };
      this.locationService.setSelectedLocation(selectedLocation);
      this.selectedLocation = selectedLocation;
    } else {
    }
  }

  getUserLocation() {
    if (navigator) {
      navigator.geolocation.getCurrentPosition(pos => {
        this.userLocation.lng = pos.coords.longitude;
        this.userLocation.lat = pos.coords.latitude;
        this.handlePFTableChange();
      });
    }
  }

  private subscribeUserSelectedLocation() {
    return this.locationService.selectedLocation.subscribe(
      this.setSelectedLocation
    );
  }
  private setSelectedLocation = (location: GoogleLocation) => {
    if (location) {
      this.selectedLocation2 = {
        lat: location.lat,
        lng: location.lng,
        zoom: 13
      };
    }
  };

  private handleSelectedLocationChange = (): Observable<any[]> => {
    return this.searchForm.controls.city.valueChanges.pipe(
      switchMap((address: string) => {
        return this.geoLocationService.getPositions(address);
      }),
      map((data: any) => {
        return data && data;
      }),
      catchError((error: any) => {
        return of([]);
      }),
      tap((data: any[]) => {
        this.searchedLocations = data;
      })
    );
  };

  setMyLocalization() {
    if (!this.searchForm.controls.myLocalization.value) {
      this.searchForm.controls.city.disable();
    } else {
      this.searchForm.controls.city.enable();
    }
  }

  getLoggedUser() {
    if (this.authService.isLogged()) {
      this.userService.getLoggedUser().subscribe(response => {
        this.loggedUser = response;
      });
    }
  }

  unbook(match) {
    this.matchService.unbookPF(match.id).subscribe(val => {
      match.isBooked = false;
      match.size = 0;
      match.isPrivate = false;
      match.ownerId = null;
      match.ownerUsername = null;
    });
  }
  checkMatch(match: MatchWithLocationDto) {
    const dialogRef = this.dialog.open(TeamsDialogComponent, {
      width: "800px",
      data: {
        teamsDto: this.teamService.getTeamsByMatch(match.id),
        isPrivate: match.isPrivate,
        matchId: match.id,
        pfId: match.pfId,
        editable: match.editable,
        ownerId: match.ownerId
      }
    });
    dialogRef.afterClosed().subscribe(val => {
      match.size = val;
    });
  }

  makeReserveation(match: MatchWithLocationDto) {
    if (!match.isBanned) {
      const dialogRef = this.dialog.open(ReservationDialogComponent, {
        width: "400px",
        data: { match: match, loggedUser: this.loggedUser }
      });
      dialogRef.afterClosed().subscribe(val => {
        if (val) {
          match.isPrivate = val.isPrivate;
          match.isBooked = true;
          match.size = 1;
          match.ownerUsername = this.loggedUser.username;
          match.ownerId = this.loggedUser.username;
          this.matchWithLocationDtoTable.renderRows();
        }
      });
    }
  }

  handlePFTableChange() {
    const searchDto = new SearchDto(
      moment(this.searchForm.value.fromDate).format("YYYY-MM-DD"),
      moment(this.searchForm.value.toDate).format("YYYY-MM-DD"),
      this.searchForm.value.fromTime,
      this.searchForm.value.toTime,
      this.searchForm.value.showPrivate,
      this.searchForm.value.showFull,
      this.searchForm.value.reserved
    );
    let searchParams;
    if (this.searchForm.controls.myLocalization.value) {
      searchParams = new SearchParams(
        this.userLocation.lat,
        this.userLocation.lng,
        this.searchForm.value.rangeInKm,
        searchDto
      );
    } else {
      console.log(this.selectedLocation);
      if (!this.searchForm.controls.city.value || !this.selectedLocation|| !this.selectedLocation.lat || !this.selectedLocation.lng) {
        this.toastrService.error(
          "Uzupełnij poprawnie miejscowość wybierając go z listy po kliknięciu w pole miejscowość i rozpoczęciu wpisywania miejscowości"
        );
        return;
      }  else {
        searchParams = new SearchParams(
          this.selectedLocation.lat,
          this.selectedLocation.lng,
          this.searchForm.value.rangeInKm,
          searchDto
        );
      }
    }

    localStorage.setItem("searchParams", JSON.stringify(searchParams));
    if (searchParams) {
      this.matchPaginator.pageSize = 10;
      this.loading = true;

      merge(
        this.sort.sortChange,
        this.matchPaginator.page,
        this.matchPaginator.pageSize
      )
        .pipe(
          startWith({}),
          switchMap(() => {
            const params = {
              sort: [`matchFromDate,asc`, `matchFromTime,asc`],
              page: this.matchPaginator.pageIndex + "",
              size: this.matchPaginator.pageSize + ""
            };
            return this.matchService.getMatchesByLocation(searchParams, params);
          }),
          map((data: any) => {
            this.matchLength = data.totalElements;
            return data.content;
          }),
          catchError(() => {
            return of([]);
          })
        )
        .subscribe(data => {
          this.matchesWithLocationDto = data;
          this.loading = false;
        });
    }
  }
  goToUserProfile(id) {
    this.dataSharingService.changeUser(id.toString());
    this.router.navigate(["users/" + id]);
  }
}
